.navbar {
  padding: 10px 20px;

}

.nav-link:hover {
  color: #fff !important;
}

.navbar-brand img {
  margin: 10px;
  
}

.card-style {
  max-width: 500px;
  width: 90%;
  min-height: 300px;
  padding: 10px;
  margin: 30px auto;
  box-shadow: 0 2rem 3rem rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  border: 20px solid #f7e8f0 !important;
  font-weight: 300;
  

}
.card-title {
  text-align: center;
  letter-spacing: 1px;
  opacity: .8;
}

.json {
  white-space: break-spaces;
  
}
.home-image {
  display: flex;
  margin: 20px auto;
  width: 65% !important;
  height: auto;
}

.home-p {
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  text-align: center;
  letter-spacing: 2px;
  font-weight: 400;
}

.ammount-input {
  margin-bottom: 10px;
}

.table {
  display: flex;
  flex-direction: column;
  margin: 20px auto !important;
  width: 90% !important;
}



